/* eslint-disable no-nested-ternary */
/* eslint-disable camelcase */
import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import { Carousel } from 'react-responsive-carousel';
import { toast } from 'react-toastify';
// import { push, goBack } from 'react-router-redux';
import './MarketPlace.css';
import ShowMoreText from 'react-show-more-text';
// import ZoomImage from '../../../common/Components/ZoomImage';
import Image from '../../../common/Components/Image';
import Loader from '../../../common/Components/Loader';
import {
  GET_RESELLING_PRODUCTS, SET_PRODUCTS, SET_PUBLIC_PAGE_PRODUCTS, SET_RESELLING_PRODUCTS,
} from '../../../constants/actionTypes';
import { APP_NAME, ERROR_MSG } from '../../../constants/otherConstants';
import agent from '../../../agent';
import defaultLogo from '../../../assets/Images/instalink_logo.png';
import CreateProduct from './CreateProduct';

class ResellingProduct extends Component {
  constructor(props) {
    super(props);
    this.state = {
      product: { product: {}, seller: {} },
      loading: false,
      user: {},
      minPrice: 0,
      maxPrice: 0,
      isSizesAvailable: false,
      isOwnProduct: false,
    };
    this.chooseProduct = this.chooseProduct.bind(this);
    this.removeProduct = this.removeProduct.bind(this);
  }

  componentDidMount() {
    const { product, user } = this.props;
    if (product) {
      if (user && user.user) {
        if (product.product.user === user.user.id) {
          this.setState({ product, isOwnProduct: true });
        } else {
          this.setState({ product, isOwnProduct: false });
        }
      }
      if (product.product && product.product.sizes_available) {
        this.setState({ isSizesAvailable: product.product.sizes_available });
        this.setMinMaxPrice(product.product.sizes);
      }
    }
    if (user && user.user) {
      this.setState({ user: user.user });
    }
  }

  componentWillReceiveProps(np) {
    const { product, user } = this.props;
    if (np.user && np.user.user !== user) {
      this.setState({ user: np.user.user });
    }
    
    if (np.product && np.product !== product) {
      if (np.user && np.user.user) {
        if (np.product.product.user === np.user.user.id) {
          this.setState({ product: np.product, isOwnProduct: true });
        } else {
          this.setState({ product: np.product, isOwnProduct: false });
        }
        if (np.product.product && np.product.product.sizes_available) {
          this.setState({ isSizesAvailable: np.product.product.sizes_available });
          this.setMinMaxPrice(np.product.product.sizes);
        }
      }
    }
  }

  chooseProduct() {
    const { product } = this.state;
    const { id } = product.product;
    const data = {
      product_id: id,
    };
    const {
      products, setProducts, publicPageProducts, setPublicPageProducts, chosenCategory, publicPageChosenCategory, setResellingProducts, resellingProducts,
    } = this.props;
    this.setState({ loading: true });

    agent.ProductsPage.addResaleProductToShop(data).then((res) => {
      toast.info('Product Added to Resale');
      this.setState({ loading: false });
      const tempresellingProducts = resellingProducts.map((p) => {
        if (p.product.id === product.product.id) {
          return { ...p, added: true };
        }
        return p;
      });
      setResellingProducts({ products: tempresellingProducts });
      if (products) {
        if (!chosenCategory || chosenCategory === product.product.category.name) {
          setProducts({ products: [product.product, ...products] });
        }
      }
      if (publicPageProducts) {
        if (!publicPageChosenCategory || publicPageChosenCategory === product.product.category.name) {
          setPublicPageProducts({ products: [product.product, ...publicPageProducts] });
        }
      }
    }).catch((err) => {
      this.setState({ loading: false });
      console.log(err, err.response);
      if (err.response && err.response.data.error) {
        toast.error(err.response.data.error);
      } else {
        toast.error(ERROR_MSG);
      }
    });
  }

  setMinMaxPrice(sizes) {
    if (!sizes || sizes.length <= 0) {
      this.setState({ minPrice: 0, maxPrice: 0 });
      return;
    }
    if (!sizes[0].price) return;
    let minPrice = sizes[0].disc_price;
    let maxPrice = sizes[0].disc_price;
    let stockAvailable = false;
    sizes.forEach((p) => {
      minPrice = Math.min(p.disc_price, minPrice);
      maxPrice = Math.max(p.disc_price, maxPrice);
      stockAvailable = stockAvailable || p.stock > 0;
    });
    this.setState({ minPrice, maxPrice, stockAvailable });
  }

  removeProduct() {
    const { product } = this.state;
    const { id } = product.product;
    const data = {
      product_id: id,
    };
    const {
      products, setProducts, publicPageProducts, setPublicPageProducts, resellingProducts, setResellingProducts,
    } = this.props;
    this.setState({ loading: true });
    agent.ProductsPage.removeResaleProductToShop(data).then(() => {
      toast.info('Product Removed from Resale');
      this.setState({ loading: false });
      const tempresellingProducts = resellingProducts.map((p) => {
        if (p.product.id === product.product.id) {
          return { ...p, added: false };
        }
        return p;
      });
      setResellingProducts({ products: tempresellingProducts });
      if (products) {
        const temp = products.filter((p) => p.id !== product.product.id);
        setProducts({ products: temp });
      }

      if (publicPageProducts) {
        const temp = publicPageProducts.filter((p) => p.id !== product.product.id);
        setPublicPageProducts({ products: temp });
      }
    }).catch((err) => {
      this.setState({ loading: false });
      console.log(err, err.response);
      if (err.response && err.response.data.error) {
        toast.error(err.response.data.error);
      } else {
        toast.error(ERROR_MSG);
      }
    });
  }

  render() {
    const {
      loading, isOwnProduct, minPrice, maxPrice, isSizesAvailable,
    } = this.state;
    const { product, seller, added } = this.state.product;
    const {
      description, disc_price, images, name, preview_images, resell_margin, price,
    } = product;

    return (
      <div className="reselling-product-div">
        <div className="relative">

          <div className="product-header">
            <div className="seller-div">
              <div className="seller-logo">
                <img src={seller.header_icon || defaultLogo} alt="" />
              </div>
              <div className="seller-name">
                {seller.header_text || APP_NAME}
              </div>
            </div>
            <div className="margin">
              {`margin: ${resell_margin} Rs`}
            </div>
          </div>
          <div className="product-img-carousel">
            <Carousel className="" style={{ background: 'white' }} showArrows showIndicators={false} showThumbs={false}>
              { images ? images.map((img, i) => (
              // <ZoomImage>
                <Image src={img} alt="" overlaySrc={preview_images[i]} key={i} />
              // </ZoomImage>
              )) : null}
            </Carousel>
          </div>
          <div className="product-footer">

            <div className="content">

              <b>{name}</b>
              {/* <p>{`${disc_price} Rs.`}</p> */}
              {
           !isSizesAvailable ? (
             <p>{`Rs.${disc_price}`}</p>
           ) : (
             minPrice !== maxPrice
               ? (
                 <p>
                   {`Rs. ${minPrice} ~ Rs.${maxPrice}`}
                 </p>
               )
               : (
                 <p>{`Rs.${maxPrice}`}</p>
               )

           )
          }
              <ShowMoreText
                lines={2}
                more="See more"
                less="See less"
                anchorClass="show-more"
                expanded={false}
                width={250}
              >
                {description}
              </ShowMoreText>
            </div>
            <CreateProduct data={product} update isResellingProduct={!isOwnProduct} isOwnProduct={added || isOwnProduct} chooseForReselling={this.chooseProduct}/>

          </div>

        </div>
        <div className='buttons' >
          {
            isOwnProduct
              ? (
                <button type="button" className="relative own-product">
                  This is your product

                </button>
              )
              : (
                <>
                  {
                    added
                      ? (
                        <button type="button" className="relative remove" onClick={this.removeProduct}>
                          Remove Product
                        </button>
                      )
                      : (
                        <button type="button" className="relative" onClick={this.chooseProduct}>
                          Resale Product
                        </button>
                      )
                  }
                </>
              )
          }
        </div>

        {
            loading
              ? <Loader />
              : null
        }

      </div>
    );
  }
}


const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
  products: state.ProductsPageReducers.products,
  productsPageNum: state.ProductsPageReducers.productsPageNum,
  publicPageProducts: state.HomePageReducers.publicPageProducts,
  chosenCategory: state.ProductsPageReducers.chosenCategory,
  publicPageChosenCategory: state.HomePageReducers.publicPageChosenCategory,
  resellingProducts: state.ProductsPageReducers.resellingProducts,
});

const mapDispatchToProps = (dispatch) => ({
  getResellingProducts: (pageNumber) => dispatch({ type: GET_RESELLING_PRODUCTS, pageNumber, payload: agent.ProductsPage.getResellingProducts(pageNumber) }),
  setResellingProducts: (products) => dispatch({ type: SET_RESELLING_PRODUCTS, payload: products }),
  setProducts: (products) => dispatch({ type: SET_PRODUCTS, payload: products }),
  setPublicPageProducts: (products) => dispatch({ type: SET_PUBLIC_PAGE_PRODUCTS, payload: products }),
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(ResellingProduct);
